.favorite-box {
  width: 100%;
  min-height: 100vh;
}

.favorite-box h2 {
  color: #111;
  font-size: 32px;
  font-weight: 600;
  margin-bottom: 15px;
}


.favorite-items {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 20px;

}

.favorite-none-items {
  text-align: center;
}

.favorite-none-items img {
  margin-bottom: 10px;
}